/* --- Neon Cyber Theme + Glow Animations --- */
* { box-sizing: border-box; }

:root{
  --bg0:#080b12;        /* deep space */
  --bg1:#0b1020;
  --fg:#e6f6ff;         /* near-white */
  --muted:#9fb3c8;
  --grid:rgba(255,255,255,.06);
  --brand:#00e5ff;      /* neon cyan */
  --brand2:#7c4dff;     /* neon purple */
  --accent:#14f195;     /* neon green */
  --line:rgba(255,255,255,.14);
}

html, body { height: 100%; }
body{
  margin:0;
  color:var(--fg);
  font-family: Inter, system-ui, Arial, sans-serif;
  background: radial-gradient(1000px 700px at 10% 10%, rgba(124,77,255,.18), transparent 60%),
              radial-gradient(900px 600px at 90% 80%, rgba(0,229,255,.18), transparent 60%),
              linear-gradient(120deg, rgba(0,229,255,.07), rgba(124,77,255,.07) 60%, rgba(20,241,149,.06));
  background-color: var(--bg0);
  animation: gradientShift 22s linear infinite;
}

/* soft grid overlay */
body::after{
  content:"";
  position:fixed; inset:0;
  pointer-events:none;
  background-image:
    linear-gradient(to right, var(--grid) 1px, transparent 1px),
    linear-gradient(to bottom, var(--grid) 1px, transparent 1px);
  background-size:38px 38px;
  opacity:.35;
  z-index:-1;
}

@keyframes gradientShift{
  0%{background-position:0 0, 100% 100%, 0 0}
  50%{background-position:100% 0, 0 100%, 0 0}
  100%{background-position:0 0, 100% 100%, 0 0}
}

.container { max-width: 880px; margin: 48px auto; padding: 0 16px; }
.title{
  font-size:42px; margin:0 0 16px;
  background: linear-gradient(90deg, var(--brand), var(--brand2));
  -webkit-background-clip:text; background-clip:text; color:transparent;
}
.badge{ filter: drop-shadow(0 0 8px rgba(20,241,149,.65)); }

.row{ display:flex; gap:10px; margin-bottom:14px; flex-wrap:wrap; }

.input{
  flex:1; min-width:220px;
  padding:10px 12px; border:1px solid var(--line);
  border-radius:12px; background:rgba(11,16,32,.6); color:var(--fg);
  backdrop-filter: blur(6px);
  transition: box-shadow .2s ease, border-color .2s ease, background .2s ease;
}
.input::placeholder{ color:#94a3b8; }
.input:focus{
  outline:none;
  border-color: rgba(0,229,255,.8);
  box-shadow: 0 0 0 2px rgba(0,229,255,.35), 0 0 22px rgba(0,229,255,.28);
  background: rgba(11,16,32,.8);
}

/* Buttons, chips, pills â€“ all glow */
.btn, .pill, .chip, .icon{
  position:relative;
  padding:10px 14px;
  color:var(--fg);
  background: rgba(11,16,32,.6);
  border:1px solid rgba(0,229,255,.45);
  border-radius:12px;
  cursor:pointer;
  transition: transform .15s ease, box-shadow .25s ease, border-color .2s ease, background .2s ease;
  box-shadow: 0 0 0 rgba(0,229,255,0);
  backdrop-filter: blur(6px);
}
.pill, .chip{ padding:6px 10px; border-radius:999px; }
.icon{ padding:6px 10px; font-size:16px; }

.btn:hover, .pill:hover, .chip:hover, .icon:hover{
  transform: translateY(-1px);
  border-color: rgba(0,229,255,.9);
  box-shadow: 0 0 18px rgba(0,229,255,.35), 0 0 36px rgba(124,77,255,.25);
}
.btn:active, .pill:active, .chip:active, .icon:active{
  transform: translateY(0);
  box-shadow: inset 0 0 12px rgba(0,229,255,.45);
}
.pill.active, .chip.active{
  color: var(--brand);
  background: rgba(0,229,255,.10);
  border-color: rgba(0,229,255,.9);
  animation: glowPulse 2.2s ease-in-out infinite;
}

/* subtle outline â€œringâ€ behind buttons */
.btn::after, .pill::after, .chip::after{
  content:""; position:absolute; inset:-6px; border-radius: inherit;
  background: radial-gradient(40% 60% at 50% 50%, rgba(0,229,255,.35), transparent 60%),
              radial-gradient(60% 90% at 50% 50%, rgba(124,77,255,.25), transparent 60%);
  opacity:0; z-index:-1; transition: opacity .25s ease;
}
.btn:hover::after, .pill:hover::after, .chip:hover::after{ opacity:.9; }

@keyframes glowPulse{
  0%,100%{ box-shadow: 0 0 16px rgba(0,229,255,.40), 0 0 34px rgba(124,77,255,.28); }
  50%{     box-shadow: 0 0 28px rgba(0,229,255,.65), 0 0 58px rgba(124,77,255,.40); }
}

/* Cards & list items (glassmorphism) */
.card{
  border:1px solid var(--line); border-radius:14px;
  background: rgba(11,16,32,.55); padding:14px 16px; margin:16px 0;
  backdrop-filter: blur(8px);
}
.list{ list-style:none; padding:0; margin:8px 0 0; }
.item{
  display:flex; align-items:center; gap:10px;
  padding:10px 12px; margin-bottom:8px;
  border:1px solid var(--line); border-radius:12px;
  background: rgba(11,16,32,.55);
  backdrop-filter: blur(8px);
}
.text{ flex:1; }
.text.done{ text-decoration: line-through; color: var(--muted); }

.controls{
  display:flex; align-items:center; justify-content:space-between;
  margin:8px 0 4px; flex-wrap:wrap; gap:8px;
}
.filters{ display:flex; gap:6px; }
.link{
  border:none; background:transparent; color:var(--brand);
  cursor:pointer; padding:6px 8px; border-radius:8px;
}
.link:hover{ text-decoration: underline; }

/* Pomodoro bits you already had */
.pomodoro{ display:grid; gap:10px; }
.pomodoro__head{ display:flex; justify-content:space-between; align-items:center; }
.pomodoro__modes{ display:flex; gap:6px; }
.pomodoro__task{ color:var(--muted); }
.pomodoro__timer{ font-size:56px; font-weight:700; text-align:center; letter-spacing:1px; }
.pomodoro__controls{ display:flex; gap:8px; justify-content:center; }
.pomodoro__settings{ margin-top:6px; }
.grid2{ display:grid; grid-template-columns: repeat(auto-fit, minmax(180px,1fr)); gap:10px; }
.rowFlex{ display:flex; align-items:center; gap:8px; }
.muted{ color:var(--muted); }

/* Accessibility */
@media (prefers-reduced-motion: reduce){
  *{ animation: none !important; transition: none !important; }
}
/* --- Fullscreen animated gradient (below the canvas for depth) --- */
body{
  background:
    radial-gradient(1000px 700px at 10% 10%, rgba(124,77,255,.16), transparent 60%),
    radial-gradient(900px 600px at 90% 80%, rgba(0,229,255,.16), transparent 60%),
    linear-gradient(120deg, rgba(0,229,255,.06), rgba(124,77,255,.06) 60%, rgba(20,241,149,.05));
  background-color:#080b12;
}

/* Live canvas layer */
.bg-canvas{
  position:fixed; inset:0; width:100%; height:100%;
  z-index:-1; pointer-events:none;
  /* slight blur beneath content for depth */
  filter: saturate(120%) contrast(110%);
}

/* Brand header */
.brand{
  position: sticky; top: 0; z-index: 2;
  display:flex; flex-direction:column; align-items:center; justify-content:center;
  padding: 18px 12px 8px;
  background: linear-gradient(180deg, rgba(8,11,18,.85), rgba(8,11,18,0));
  backdrop-filter: blur(6px);
}
.brand__logo{
  font-size: 30px; font-weight: 800; letter-spacing: .5px;
  background: linear-gradient(90deg, #00e5ff, #7c4dff 60%, #14f195);
  -webkit-background-clip:text; background-clip:text; color:transparent;
  text-shadow: 0 0 22px rgba(0,229,255,.25);
  filter: drop-shadow(0 0 10px rgba(124,77,255,.25));
}
.brand__logo span{ opacity:.9; }
.brand__tag{
  margin: 6px 0 0; font-size: 13px; color:#9fb3c8;
  letter-spacing: .4px;
}

/* Stronger glow on primary actions */
.btn{
  border-color: rgba(0,229,255,.65);
  box-shadow:
    0 0 12px rgba(0,229,255,.25),
    inset 0 0 0 rgba(0,229,255,0);
}
.btn:hover{
  box-shadow:
    0 0 20px rgba(0,229,255,.45),
    0 0 40px rgba(124,77,255,.35);
}
.btn:active{
  box-shadow: inset 0 0 16px rgba(0,229,255,.55);
}

/* Fancy hover trail on pills/chips */
.pill:hover, .chip:hover{
  background: linear-gradient(90deg, rgba(0,229,255,.12), rgba(124,77,255,.12));
}

/* Optional: subtle â€œfloatâ€ of cards */
.card, .item {
  transition: transform .25s ease, box-shadow .25s ease;
}
.card:hover, .item:hover{
  transform: translateY(-2px);
  box-shadow: 0 6px 28px rgba(0,0,0,.25), 0 0 22px rgba(124,77,255,.2);
}
/* === Phase 1 & 2 UI bits === */
.emoji-btn{
  background: rgba(255,255,255,.05);
  border: 1px solid rgba(255,255,255,.12);
  width: 40px; height: 40px; border-radius: 10px;
  font-size: 22px; cursor: pointer;
  transition: transform .08s ease;
}
.emoji-btn:active{ transform: scale(.96); }

.tip{
  background: rgba(0,0,0,.25);
  border: 1px dashed rgba(255,255,255,.12);
  padding: 8px 10px; border-radius: 10px;
}

.goals{ list-style:none; padding:0; margin:10px 0 0 0; display:flex; flex-direction: column; gap:8px;}
.goals .goal-emoji{ font-size:18px; margin-right:6px;}
.goals .goal-title{ font-weight:600; margin-right:8px;}
.goals .goal-stat{ margin-left:6px; font-size:12px; opacity:.8; }
.goals .bar{ flex:1; height:8px; border-radius:999px; background:rgba(255,255,255,.1); overflow:hidden; margin:0 8px;}
.goals .fill{ height:100%; background: var(--accent); }

.matrix{
  display: grid;
  grid-template-columns: repeat(2, minmax(260px, 1fr));
  gap: 12px;
}
.matrix-cell{
  border: 2px solid rgba(255,255,255,.2);
  border-radius: 12px;
  padding: 10px;
  background: rgba(0,0,0,.2);
  backdrop-filter: blur(8px);
}
.matrix-title{ font-size: 12px; opacity: .8; margin-bottom: 6px; }

@media (max-width: 720px){
  .matrix{ grid-template-columns: 1fr; }
}
/* Nav + FAQ + Matrix bits */
