.nav__brand{font-weight:700;letter-spacing:.2px}.nav__brand span{color:var(--accent)}
.nav__links{display:flex;gap:10px;align-items:center}
.nav__link{padding:.35rem .6rem;border-radius:999px;border:1px solid transparent}
.nav__link:hover{border-color:rgba(255,255,255,.2)}
.nav__link.active{background:rgba(255,255,255,.12);border-color:rgba(255,255,255,.2)}

details.faq{border:1px solid rgba(255,255,255,.12);border-radius:10px;padding:.5rem .75rem;margin:.4rem 0}
details.faq summary{cursor:pointer}
details.faq[open]{background:rgba(0,0,0,.2)}

