/* High-contrast, animated page titles */
.page-header { margin-bottom: .5rem; }
.page-title {
  display: flex; align-items: center; gap: 10px;
  font-size: clamp(1.4rem, 2.8vw, 2.2rem);
  line-height: 1.2;
  color: #f8fafc;
  text-shadow: 0 1px 0 rgba(0,0,0,.4);
}
.page-icon { font-size: 1.3em; }
.title-anim {
  background: linear-gradient(90deg, #e2e8f0, var(--accent), #e2e8f0);
  -webkit-background-clip: text; background-clip: text;
  color: transparent;
  background-size: 200% 100%;
  animation: shimmer 3s linear infinite;
}
@keyframes shimmer { 0%{background-position:0% 50%} 100%{background-position:200% 50%} }
.page-subtitle { margin: .1rem 0 .6rem; opacity: .85; }

/* Icon button (bell, email) */
.icon-btn{
  background: transparent; border: 1px solid rgba(255,255,255,.2);
  border-radius: 999px; padding: .3rem .6rem; cursor: pointer;
}
.icon-btn:hover{ background: rgba(255,255,255,.1); }
.nav__owner{ font-size: 12px; opacity: .8; }

/* Wellness visible controls */
.wellness-row{ display:flex; gap:16px; align-items:center; flex-wrap:wrap; }
.mood-group .label, .stress-group .label{ font-size:12px; opacity:.8; margin-bottom:6px; }
.mood-choices{ display:flex; gap:10px; }
.emoji-big{
  width:56px; height:56px; border-radius:14px; font-size:28px;
  background: rgba(255,255,255,.05); border:2px solid rgba(255,255,255,.12);
  cursor:pointer; transition:transform .08s ease, border-color .2s ease;
}
.emoji-big.selected{ border-color: var(--accent); transform: translateY(-1px); }
.stress-group input[type="range"]{ width: 260px; }
.stress-scale{ display:flex; justify-content:space-between; width:260px; font-size:18px; margin-top:4px; }
.tip-actions{ display:flex; gap:8px; margin-top:8px; }

/* Goals symbols grid */
.emoji-grid{ display:grid; grid-template-columns: repeat(auto-fill, minmax(40px, 1fr)); gap:6px; max-width:320px; }
.emoji-pick{
  width:40px; height:40px; border-radius:10px; font-size:20px; cursor:pointer;
  border:1px solid rgba(255,255,255,.18); background: rgba(255,255,255,.05);
}
.emoji-pick.selected{ border-color: var(--accent); background: rgba(255,255,255,.12); }
.goal-input-row{ display:flex; gap:8px; margin-top:8px; flex-wrap:wrap; }

/* Make legacy titles more visible */
.title, .card h2 { color: #f8fafc; text-shadow: 0 1px 0 rgba(0,0,0,.35); }


